<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-input
            type="text"
            placeholder=""
            v-model="filter_data.filter_submitter"
            maxlength="64"
            show-word-limit
            style="width:180px;"
        >
        </el-input>
        <el-input
            type="text"
            placeholder=""
            v-model="filter_data.filter_content"
            maxlength="64"
            show-word-limit
            style="width:180px;"
        >
        </el-input>
        <el-input
            type="text"
            placeholder=""
            v-model="filter_data.filter_follow_up_person"
            maxlength="64"
            show-word-limit
            style="width:180px;"
        >
        </el-input>
        <el-input
            type="text"
            placeholder=""
            v-model="filter_data.filter_solver"
            maxlength="64"
            show-word-limit
            style="width:180px;"
        >
        </el-input>
        <el-input
            type="text"
            placeholder=""
            v-model="filter_data.filter_cause_detail"
            maxlength="64"
            show-word-limit
            style="width:180px;"
        >
        </el-input>
        <el-select v-model="filter_data.filter_priority" placeholder="bug" style="width:180px;">
          <el-option
              v-for="(value, key) in bugPriority"
              :label="key + '' + value + ''"
              :value="key"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_level" placeholder="bug" style="width: 180px">
          <el-option
              v-for="(value, key) in bugLevel"
              :label="key + '' + value + ''"
              :value="key"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_classification" placeholder="bug" style="width: 180px">
          <el-option
              v-for="(value, key) in bugClassification"
              :label="value"
              :value="parseInt(key)"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_cause" placeholder="bug" style="width: 180px">
          <el-option
              v-for="(value, key) in bugCause"
              :label="value"
              :value="parseInt(key)"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_department" placeholder="bug" style="width: 180px">
          <el-option
              v-for="(value, key) in bugDepartment"
              :label="value"
              :value="parseInt(key)"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_result" placeholder="bug" style="width: 180px">
          <el-option
              v-for="(value, key) in bugResult"
              :label="value"
              :value="parseInt(key)"
              :key="key"
          >
          </el-option>
        </el-select>
        <el-select v-model="filter_data.filter_is_push" placeholder="" style="width: 180px">
          <el-option
              v-for="(value, key) in bugIsPush"
              :label="value"
              :value="parseInt(key)"
              :key="key"
          >
          </el-option>
        </el-select>
      </el-row>
      <el-row>
        <el-button type="primary" icon="el-icon-search" @click="pageChange(1, 10)"> </el-button>
        <el-button type="primary" @click="resetFilter()"> </el-button>
      </el-row>
      <el-row style="margin-top: 10px;">
        <el-button type="success" @click="linkToAddBug()" style="margin-bottom: 20px;">bug</el-button>
        <el-button type="success" icon="el-icon-download" @click="downloadExcel()" style="margin-bottom: 20px;"></el-button>
      </el-row>
    </el-card>

    <el-card class="box-card">
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" fixed="left"></el-table-column>
        <el-table-column prop="id" label="" width="50" fixed="left">
          <!--         <template slot-scope="scope">{{ scope.row.id }}</template>-->
        </el-table-column>
        <el-table-column prop="submitter" label="" width="80"></el-table-column>
        <el-table-column prop="content" label="" width="300" show-overflow-tooltip></el-table-column>
        <el-table-column prop="priority" label="bug" width="100">
          <template slot-scope="scope">{{ scope.row.priority + "(" + bugPriority[scope.row.priority] + ")" }}</template>
        </el-table-column>
        <el-table-column prop="level" label="" width="100">
          <template slot-scope="scope">{{ scope.row.level + "(" + bugLevel[scope.row.level] + ")" }}</template>
        </el-table-column>
        <el-table-column prop="classification" label="" width="100">
          <template slot-scope="scope">{{ bugClassification[scope.row.classification] }}</template>
        </el-table-column>
        <el-table-column prop="cause" label="" width="100">
          <template slot-scope="scope">{{ bugCause[scope.row.cause] }}</template>
        </el-table-column>
        <el-table-column prop="department" label="" width="100">
          <template slot-scope="scope">{{ bugDepartment[scope.row.department] }}</template>
        </el-table-column>
        <el-table-column prop="result" label="" width="120">
          <template slot-scope="scope">{{ bugResult[scope.row.result] }}</template>
        </el-table-column>
        <el-table-column prop="follow_up_person" label="" width="100"></el-table-column>
        <el-table-column prop="solver" label="" width="100"></el-table-column>
        <el-table-column prop="cause_detail" label="" show-overflow-tooltip width="300"></el-table-column>
        <el-table-column prop="update_user.username" label="" width="100"></el-table-column>
        <el-table-column prop="update_time" label="" width="100"></el-table-column>
        <el-table-column prop="create_user.username" label="" width="100"></el-table-column>
        <el-table-column prop="create_time" label="" width="100"></el-table-column>
        <el-table-column prop="is_push" label="">
          <template slot-scope="scope">
            <el-tag :type="bugPushTagFilter[scope.row.is_push]">{{ bugIsPush[scope.row.is_push] }} </el-tag>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="200">
          <template slot-scope="scope">
            <el-button @click="bugPush(scope.row.id)" type="success" size="mini" plain round></el-button>
            <el-button @click="editDetail(scope.row.id)" type="primary" size="mini" plain round style="margin-left: 0;"></el-button>
            <el-popconfirm
                confirm-button-text=''
                cancel-button-text=''
                icon="el-icon-info"
                icon-color="red"
                title=""
                @confirm="deleteClick(scope.row.id)"
            >
              <el-button type="danger" size="mini" slot="reference" plain round></el-button>
            </el-popconfirm>


          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--   -->
    <div class="pageSet">
      <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageInfo.current_page"
          :page-sizes="[10, 20, 30, 40, 50, 100, 200, 500, 1000]"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageInfo.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import XLSX from 'xlsx'
import * as XLSX from 'xlsx/xlsx.mjs'
export default {
  name: "Bug_list",
  data(){
    return{
      pageInfo: {
        page: 1,
        size: 10,
        total: '',
        current_page: 1,   // 
      },
      tableData: [],    // bug
      multipleSelection: [],  // 
      bugPriority:{
        // bug
        a: '',
        b: '',
        c: '',
        d: '',
      },
      bugLevel: {
        // bug
        a: "",
        b: "",
        c: "",
        d: "",
      },
      bugClassification: {
        // bug
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: "",
        7: "DB",
        8: "",
      },
      bugCause: {
        // bug
        0: "",
        1: "",
        2: "",
        3: "",
        4: "",
        5: "",
        6: "",
        7: "",
      },
      bugDepartment: {
        // 
        0: "",
        1: "iOS",
        2: "",
        3: "server",
        4: "",
        5: "web",
        6: "",
        7: "appH5",
        8: "",
        9: "",
        10: "",
        11: "",
        12: "",
      },
      bugResult: {
        // bug
        0: "",
        1: "",
        2: "bug",
        3: "",
        4: "",
        5: "",
        6: "",
        7: "",
      },
      bugIsPush: {
        // bug
        0: "",
        1: ""
      },
      bugPushTagFilter: {
        // 01
        0: "danger",
        1: "success"
      },
      filter_data: {
        filter_submitter: '',  // 
        filter_content: '',   // 
        filter_follow_up_person: '',   // 
        filter_solver: '',   // 
        filter_cause_detail: '',   // 
        filter_priority: '',   // 
        filter_level: '',   // 
        filter_classification: '',  // 
        filter_cause: '',   // 
        filter_department: '',   // 
        filter_result: '',    // 
        filter_is_push: '',    // 
      },
      excel_data: [],
    }
  },
  methods: {
    pageChange(page, size){
      this.axios.get(
          "bug/bug_list/",
          {
            params:{
              page:page,
              size:size,
              submitter: this.filter_data.filter_submitter,
              content: this.filter_data.filter_content,
              follow_up_person: this.filter_data.filter_follow_up_person,
              solver: this.filter_data.filter_solver,
              cause_detail: this.filter_data.filter_cause_detail,
              priority: this.filter_data.filter_priority,
              level: this.filter_data.filter_level,
              classification: this.filter_data.filter_classification,
              cause: this.filter_data.filter_cause,
              department: this.filter_data.filter_department,
              result: this.filter_data.filter_result,
              is_push: this.filter_data.filter_is_push,
            }
          },
      ).then(res => {
        // console.log("", res);
        // console.log("results:", res.data.results)
        this.tableData = res.data.results;
        this.pageInfo.total = res.data.count;
        this.pageInfo.page = page;
        this.pageInfo.size = size;
        this.pageInfo.current_page = page;
      }).catch(reason => {
        console.log('', reason);
      })
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pageChange(this.pageInfo.page, val);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.pageChange(val, this.pageInfo.size);
      this.pageInfo.current_page = val;
      return val
    },
    handleSelectionChange(val) {
      // this.multipleSelection
      let that = this;
      that.multipleSelection = val;
      console.log(typeof(that.multipleSelection), that.multipleSelection);
    },
    linkToAddBug(){
      this.$router.push({name: "Add_bug"});
    },
    editDetail(id){
      // console.log(id);
      this.$router.push({name: "Edit_bug", query: {id: id}});
    },
    deleteClick(id){
      this.axios.delete(
          'bug/bug_list/'+id.toString()+'/'
      ).then(function(res) {
        console.log("", res);
        location.reload();

      }).catch(reason => {
        console.log('', reason);
      });

    },
    resetFilter(){
      // 
      for(let key in this.filter_data){
        this.filter_data[key] = '';
      }
    },
    bugPush(id){
      this.axios.patch(
          "/bug/bug_push_message/" + id.toString() + "/",
          {
            "is_push": 1
          }
      ).then(res => {
        if (res.data.code === 1) {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: 'success'
          });
          location.reload();   // 
        } else if (res.data.code === 1001) {
          // {"code":1001,"detail":{"create_user":[""]},"msg":""}
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: 'error'
          });
        } else {
          this.$message({
            showClose: true,
            message: res.data.msg,
            type: 'error'
          });
          return false;
        }
      }).catch(reason => {
        console.log('', reason)})

    },
    CurrentTime(){
      var now = new Date();
      var year = now.getFullYear(); //(4,1970-????)
      var month = now.getMonth() + 1; //(0-11,01)
      var day = now.getDate(); //(1-31)
      var hour = now.getHours(); //(0-23)
      var minute = now.getMinutes(); //(0-59)
      var second = now.getSeconds(); //(0-59)
      var current_time = year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
      return current_time;
    },
    downloadExcel(){
      for (var index in this.multipleSelection){
        // 
        delete this.multipleSelection[index]["create_user"];
        delete this.multipleSelection[index]["update_user"];
        delete this.multipleSelection[index]["is_push"];
        delete this.multipleSelection[index]["status"];
        delete this.multipleSelection[index]["at_persons"];
        // 
        this.multipleSelection[index]["priority"] = this.multipleSelection[index]["priority"] + "" + this.bugPriority[this.multipleSelection[index]["priority"]] + "";
        this.multipleSelection[index]["level"] = this.multipleSelection[index]["level"] + "" + this.bugLevel[this.multipleSelection[index]["level"]] + "";
        this.multipleSelection[index]["classification"] = this.bugClassification[this.multipleSelection[index]["classification"]];
        this.multipleSelection[index]["cause"] = this.bugClassification[this.multipleSelection[index]["cause"]];
        this.multipleSelection[index]["department"] = this.bugDepartment[this.multipleSelection[index]["department"]];
        this.multipleSelection[index]["result"] = this.bugResult[this.multipleSelection[index]["result"]];
      }
      const download_data = XLSX.utils.json_to_sheet(this.multipleSelection);
      const wb = XLSX.utils.book_new();
      const current_Time = this.CurrentTime();
      XLSX.utils.book_append_sheet(wb, download_data, 'bug');
      XLSX.writeFile(wb, 'bug_list_' + current_Time + '.xlsx');
    }
  },
  created() {
    this.pageChange(1, 10)
  },

}
</script>

<style scoped>
.pageSet{
  margin-top: 20px;
  margin-left: 550px;
}
</style>